import React, { Suspense, lazy } from 'react';
import { Spinner, VerticalNavigation, BrandBand } from '@salesforce/design-system-react';

import {$, baseUrl, isDev} from '../lib/grax-data-lake';

import _ from 'lodash';

const GRAXCloudConfigurationsDataTable = lazy(() => import('./GRAXCloudConfigurationsDataTable'));

const ACTIONS = {
    LOAD_CONFIGURATIONS: 1,
    SAVE_CONFIGURATIONS: 2
};

const GRAXCLOUD_MENU = [
	{
		id: 'grax_cloud',
		label: 'GRAX Cloud',
		items: [
			//{ id: 'dashboard', label: 'Dashboard' },
            { id: 'configurations', label: 'Configurations' },
            //{ id: 'accounts', label: 'Accounts' },
			//{ id: 'users', label: 'Users' },
			//{ id: 'notifications', label: 'Notifications' }
		],
	}
];

console.log('isDev: ', isDev);

export default class GRAXCloud extends React.Component {
    static displayName = 'GRAXCloudNavigation';

    constructor(props) {
        super(props);

        this.state = {
            isLoading: true,
            error: null,
            configurations: {},

            //selectedId: 'dashboard'
            selectedId: 'configurations'
        };

        this.actionHandler = this.actionHandler.bind(this);
    }

    componentDidMount() {
        this.actionHandler(ACTIONS.LOAD_CONFIGURATIONS);
    }

    actionHandler(action, params) {
        if(action === ACTIONS.LOAD_CONFIGURATIONS) {
            if(!isDev) {
                $.ajax({
                    type: 'GET',
                    //dataType: 'jsonp',
                    url: baseUrl + '/grax-app/grax-cloud/configurations',
                    data: {},
                    success: function(data) {
                        var configurations = data || {};

                        console.log(data);

                        try {
                            configurations = $.parseJSON(data);
                        } catch(e){}

                        this.setState({
                            configurations: configurations,
                            isLoading: false
                        });
                    }.bind(this),
                    error: function(xhr, status, err) {
                        console.error(xhr, status, err);

                        var isNonError = xhr.status === 404 || xhr.status === 200;

                        this.setState({
                            error: isNonError ? null : err.toString(),
                            configurations: isNonError ? {} : null,
                            isLoading: false
                        });
                    }.bind(this)
                });
            }

            if(isDev) {
                this.setState({
                    configurations: {
                        "globalEnvironmentDefaults": {
                            "asyncAttachmentProcessorOrganizationIds": [
                                "00D4test0000Z1cUAE"
                            ],
                            "asyncBulkLoadOrganizationIds": [
                                "00D46test0172yZEAQ"
                            ],
                            "bulkRestoreDestinationOrganizationId": "00D4test0000Z1cUAE",
                            "bulkRestoreSourceOrganizationId": "00D3test000LiLXUA0",
                            "syncSalesforceEinsteinOrganizationIds": [
                                "00D46test0172yZEAQ"
                            ],
                            "syncSalesforceSourceOrganizationIds": [
                                "00D46test0172yZEAQ"
                            ],
                            "metadataBackupOrganizationIds": [
                                "00D46test0172yZEAQ"
                            ],
                            "odataOrganizationId": "00D47test0172yZEAQ"
                        },
                        "environments": {
                            "test": {
                                "dataSources": [
                                    {
                                        "userName": "sfdc@example.com",
                                        "password": "password",
                                        "token": "token",
                                        "url": "https://test.salesforce.com/",
                                        "organizationId": "00D4test0000Z1cUAE",
                                        "provider": "salesforce",
                                        "name": "Production Source SFDC"
                                    },
                                    {
                                        "userName": "sfdc@example.com",
                                        "password": "password",
                                        "token": "token",
                                        "url": "https://login.salesforce.com/",
                                        "organizationId": "00D46test0172yZEAQ",
                                        "provider": "salesforce",
                                        "name": "Production Destination SFDC"
                                    }
                                ],
                                "elastic": [
                                    {
                                        "provider": "bonsai",
                                        "name": "default",
                                        "url": "https://example.com",
                                        "maxbatchsize": 300
                                    }
                                ],
                                "storage": [
                                    {
                                        "provider": "aws",
                                        "name": "Production Storage",
                                        "accessKey": "accessKey",
                                        "secretAccessKey": "secretAccessKey",
                                        "bucket": "bucket-name",
                                        "region": "us-east-2",
                                        "storageOff": false
                                    }
                                ],
                                "syncAnalytics": [
                                    {
                                        "provider": "syncanalytics",
                                        "name": "Sync Analytics",
                                        "marketingCloudHost": "example.mc.host",
                                        "marketingCloudUser": "marketingCloudUser",
                                        "marketingCloudPassword": "mcpasswd",
                                        "marketingCloudPort": 22,
                                        "syncQueryFile": "analytics-query.json",
                                        "syncSalesforceObjects": "exampleObject",
                                        "syncBatchSize": 100
                                    }
                                ],
                                "metadataBackup": [
                                    {
                                        "sourceControlProvider": "github",
                                        "name": "Git Integration",
                                        "sourceControlEnabled": true,
                                        "isEnabled": true,
                                        "gitRepositoryName": "testrepo",
                                        "gitRepoAuthorizationToken": "testtokenabc123",
                                        "gitUserId": "testuser"
                                    }
                                ],
                                "bulkProcess": [
                                    {
                                        "name": "test"
                                    }
                                ]
                            }
                        }
                    },
                    isLoading: false
                });
            }
        }

        if(action === ACTIONS.SAVE_CONFIGURATIONS && isDev) {
            console.log(params.configurations);
        }

        if(action === ACTIONS.SAVE_CONFIGURATIONS && !isDev) {
            $.ajax({
                type: 'POST',
                //dataType: 'jsonp',
                url: baseUrl + '/grax-app/grax-cloud/configurations',
                contentType: "application/json; charset=utf-8",
                data: JSON.stringify(params.configurations),
                success: function(data) {
                    console.log(data);

                    this.setState({
                        configurations: params.configurations || {}
                    });
                }.bind(this),
                error: function(xhr, status, err) {
                    console.error(xhr, status, err);
    
                    this.setState({
                        error: err.toString()
                    });
                }.bind(this)
            });
        }
    }

    render() {
        if(this.state.isLoading) {
            return (
                <Spinner size="large" variant="brand" />
            );
        }

        if(this.state.error != null) {
            return (
                <span>{this.state.error}</span>
            );
        }

        return (
            <React.Fragment>
                <style>{`
                    #grax-cloud .slds-card__body .slds-section .slds-section__title,
                    #grax-cloud .slds-card__body .slds-section .slds-section__title-action {
                        border-radius: 0;
                    }

                    #grax-cloud .slds-card__body .slds-section .slds-section__content {
                        background: #fff;
                    }

                    #grax-cloud .slds-card__body .slds-section.slds-is-open {
                        margin: 2rem 0;
                    }

                    #grax-cloud .slds-card__body .slds-section.slds-is-open .slds-section__title {
                        border-bottom-left-radius: 0;
                        border-bottom-right-radius: 0;
                        background: transparent;
                    }

                    #grax-cloud .slds-brand-band.dsr-brand-band_lightning-blue:before {
                        background-image: url(./assets/images/themes/oneSalesforce/banner-brand-default.png), linear-gradient(to top, rgba(175, 197, 222, 0) 0, #1B5F9E)!important;
                    }
                `}</style>
            
                <div className="slds-grid" id="grax-cloud">
                    <div className="slds-col slds-size_1-of-4" style={{height: '100vh'}}>
                        <VerticalNavigation
                            id="grax-cloud-navigation"
                            categories={GRAXCLOUD_MENU}
                            selectedId={this.state.selectedId}
                            onSelect={(event, data) => {
                                this.setState({ selectedId: data.item.id });
                            }}
                        />
                    </div>

                    <div className="slds-col slds-size_3-of-4">
                    {/*<div className="slds-col">*/}
                        {/*<BrandBand
                            className="custom-brand-band-class slds-p-around_medium"
                            id="brand-band-large"
                            size="large"
                        >*/}
                        <BrandBand
                            className="custom-brand-band-class slds-p-around_medium"
                            id="brand-band-lightning-blue"
                            theme="lightning-blue"
                        >
                            {this.state.selectedId === 'configurations' ? (
                                <Suspense fallback={<Spinner size="large" variant="brand" />}>
                                    <GRAXCloudConfigurationsDataTable globalConfigurations={_.get(this.state, 'configurations.globalEnvironmentDefaults', {})} configurations={_.get(this.state, 'configurations.environments', [])} onChange={(globalConfigurations, environments) => {
                                        var configurations = this.state.configurations;

                                        configurations.globalEnvironmentDefaults = globalConfigurations;
                                        configurations.environments = environments;

                                        this.actionHandler(ACTIONS.SAVE_CONFIGURATIONS, {
                                            configurations: configurations
                                        });
                                    }} />
                                </Suspense>
                            ) : null}
                        </BrandBand>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}